//--|ðŸ ‹| Color Scheme |ðŸ ‹|--//
$primary-dark: #1f3131; //--ðŸ Š rgba(31,49,49,1) ðŸ ˆ--//
$primary-medium: #3d6262; //--ðŸ Š rgba(61,98,98,1) ðŸ ˆ--//
$primary-light: #9eb0b0; //--ðŸ Š rgba(158,176,176,1) ðŸ ˆ--//

$secondary-dark: #230510; //--ðŸ Š rgba(35,5,16,1) ðŸ ˆ--//
$secondary-medium: #450920; //--ðŸ Š rgba(69,9,32,1) ðŸ ˆ--//
$secondary-light: #a2848f; //--ðŸ Š rgba(162,132,143,1) ðŸ ˆ--//

$tertiary-dark: #392319; //--ðŸ Š rgba(57,35,25,1) ðŸ ˆ--//
$tertiary-medium: #714631; //--ðŸ Š rgba(113,70,49,1) ðŸ ˆ--//
$tertiary-light: #b8a298; //--ðŸ Š rgba(184,162,152,1) ðŸ ˆ--//

// @import '../tools/vendors/bootstrap/5.3.3/css/'
@import '../layouts/containers/color-palette.scss';
@import '../layouts/containers/global-styling.scss';

div[id*='index'] {
  height: 100vh;
  display: grid;
  /* background: linear-gradient(140deg, $primary-dark 0%, $primary-medium 75%, $primary-dark 100%); */
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    grid-template: 4rem 1fr 4rem / 6rem 1fr 6rem; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  }
  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    grid-template: 6rem 1fr 6rem / 4rem 1fr 4rem; //~~|ðŸ Š rows / columns ðŸ ˆ|~~//
  }
}

@mixin orientation-testing {
  //--|ðŸ ‹ Desktop (Landscape) ðŸ ‹|--//
  @media screen and (orientation: landscape) {
    & {
      aside[class*='leftbar'] {
        background: rgba($red-light, 0.25);
      }
      aside[class*='rightbar'] {
        background: rgba($red-light, 0.25);
      }
      section[class*='overlay'] {
        background: rgba($red-dark, 0.25);
      }

      header {
        background: rgba($red-light, 0.25);
      }
      footer {
        background: rgba($red-light, 0.25);
      }
      main {
        background: rgba($red-dark, 0.25);
      }
    }

    //~~|ðŸ Š border: 2px dashed $red-neon; ðŸ ˆ|~~//
    //~~|ðŸ Š background: rgba($red-dark, 0.25); ðŸ ˆ|~~//
  }

  //--|ðŸ ‹ Mobile (Portrait) ðŸ ‹|--//
  @media screen and (orientation: portrait) {
    & {
      aside[class*='leftbar'] {
        background: rgba($blue-light, 0.25);
      }
      aside[class*='rightbar'] {
        background: rgba($blue-light, 0.25);
      }
      section[class*='overlay'] {
        background: rgba($blue-dark, 0.25);
      }

      header {
        background: rgba($blue-light, 0.25);
      }
      footer {
        background: rgba($blue-light, 0.25);
      }
      main {
        background: rgba($blue-dark, 0.25);
      }
    }
    //~~|ðŸ Š border: 2px dashed $blue-neon; ðŸ ˆ|~~//
    //~~|ðŸ Š background: rgba($blue-dark, 0.25); ðŸ ˆ|~~//
  }
}

body {
  height: 100vh; //~~|ðŸ Š Force body's height to fit the screen ðŸ ˆ|~~//
  overflow: hidden; //~~|ðŸ Š Hide the scrollbar completely ðŸ ˆ|~~//
  /*
  background: linear-gradient(
    140deg,
    $primary-dark 0%,
    $primary-medium 50%,
    $primary-light 100%
  ); //~~|ðŸ Š Apply the background gradient ðŸ ˆ|~~//
  */

  #index-body {
    @include orientation-testing();
  }
}
